import { loadSearchData } from '../api';

export const ACTION_TYPES = {
  SAVE_DATA: 'SAVE_DATA',
  START_LOADING: 'START_LOADING',
  STOP_LOADING: 'STOP_LOADING',
  SAVE_ERROR: 'SAVE_ERROR',
};

const saveData = data => ({
  type: ACTION_TYPES.SAVE_DATA, payload: data,
});

export const startLoading = () => ({
  type: ACTION_TYPES.START_LOADING,
});

export const stopLoading = () => ({
  type: ACTION_TYPES.STOP_LOADING,
});

export const setError = (error) => ({
  type: ACTION_TYPES.SAVE_ERROR, payload: error,
});

export const loadData = (query, count) => (dispatch) => {
  

  dispatch(startLoading())
  loadSearchData(query,count)
    .then(result => {
      if (result.hasOwnProperty('Errors')){
        return dispatch(setError(result.Errors[0].Description))
      }else if (result.Result.length === 0){
        return dispatch(setError("No mach found"))
      }
        dispatch(setError(''))
        return dispatch(saveData(result))
    })
    .catch(error => console.log(error))
    .finally(() => dispatch(stopLoading()))
};