import React from 'react';
import Box from '@material-ui/core/Box';
import Collapse from '@material-ui/core/Collapse';
import Typography from '@material-ui/core/Typography';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import IconButton from '@material-ui/core/IconButton';
import TableHead from '@material-ui/core/TableHead';
import AddShoppingCartOutlinedIcon from '@material-ui/icons/AddShoppingCartOutlined';
import TableRow from '@material-ui/core/TableRow';
import { columns } from './Table';


export const TableAnalogsRow = ({open, row}) => {

return (
  <TableRow>
    <TableCell style={{ padding: 0, border: 'none', backgroundColor: "#D7FFFE" }} colSpan={7} >
      <Collapse in={open} timeout="auto" unmountOnExit>
        <Box >
          <Typography variant="h5" gutterBottom component="div" style={{ textAlign: 'center' }}>
            Аналоги
          </Typography>
          <Table size="small" aria-label="purchases" >
            <TableHead>
              <TableRow>
                {columns.map(column => (
                  <TableCell
                    key={column.id}
                    align={column.align}
                    style={{ minWidth: column.minWidth, maxWidth: column.maxWidth }}
                  >
                    {column.label === 'Аналоги' ? '' : column.label}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {row.analogs.map((item) => (
                <TableRow key={item.Card} style={{ width: '100%' }}>
                  <TableCell component="th" scope="row">

                  </TableCell>
                  <TableCell component="th" scope="row">
                    <img src='./images/part.svg' alt="img" />
                  </TableCell>
                  <TableCell align="left">{item.Number}</TableCell>
                  <TableCell align="left">{item.DescriptionUkr}</TableCell>
                  <TableCell align="left">
                    {item.Rests.length > 0 ? item.Rests[0].Value : 0}
                  </TableCell>
                  <TableCell align="left">
                    {item.Price}
                  </TableCell>
                  <TableCell>
                    <IconButton
                      color="primary"
                    >
                      <AddShoppingCartOutlinedIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Box>
      </Collapse>
    </TableCell>
  </TableRow>
  )
}





