import React, { useState, useEffect } from 'react';
import { fade, makeStyles } from '@material-ui/core/styles';
import { useDispatch } from 'react-redux';
import { InputBase, IconButton } from '@material-ui/core/';
import SearchIcon from '@material-ui/icons/Search';
import { loadData } from '../../store/actions';
import { useHistory, useLocation } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  grow: {
    display: 'flex',
  },
  search: {
    position: 'relative',
    width: '100%',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(4),
      width: 'auto',
    },
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1),
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '30ch',
      '&:focus': {
        width: '40ch',
      },
    },
  },
}));

export const SearchField = () => {
  const classes = useStyles();
  const [query, setQuery] = useState('');
  const dispatch = useDispatch()
  const history = useHistory();
  const location = useLocation();

  const searchParams = new URLSearchParams(location.search);
  const searchQuery = searchParams.get('simple_search') || '';
  console.log(searchQuery)
  useEffect(() => dispatch(loadData(searchQuery, 10)), [dispatch,searchQuery ])
    
  const handleSubmit = (e) => {
    searchParams.set('simple_search', query);
    history.push({
      pathname: '/search',
      search: searchParams.toString(),
    });
    e.preventDefault();
    dispatch(loadData(query , 5))
    setQuery('');
  }
  return (
      <form 
        className={classes.grow}
        onSubmit={(e) => handleSubmit(e)}
      >
          <InputBase className={classes.search}
            placeholder="Введіть назву або код запчастини"
            classes={{
              root: classes.inputRoot,
              input: classes.inputInput,
            }}
            inputProps={{ 'aria-label': 'search' }}
            value={query}
            onChange={(e) => setQuery(e.target.value)}
          />
        <IconButton type='submit'>
          <SearchIcon style={{ color: 'white' }} />
        </IconButton>
      </form>
  )
};

