import React from 'react';
import { useSelector } from 'react-redux';
import { selectProductList } from '../../store/selectors';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import { Row } from './TableRow';
import { PaginationLink } from '../Pagination/Pagination';

export const columns = [
  { id: 'analogs', label: 'Аналоги', minWidth: 50, maxWidth: 100 },
  { id: 'photo', label: 'Фото', minWidth: 50, maxWidth: 100 },
  { id: 'code', label: 'Код/Аналоги', minWidth: 150, maxWidth: 200 },
  {
    id: 'name',
    label: 'Назва товару',
    minWidth: 250,
    maxWidth: 300,
    align: 'left',
  },
  {
    id: 'rest',
    label: 'Наявність',
    minWidth: 50,
    maxWidth: 100,
    align: 'left',
  },
  {
    id: 'price',
    label: 'Ціна',
    minWidth: 80,
    maxWidth: 100,
    align: 'left',
  },
  {
    id: 'buttons',
    label: '',
    minWidth: 170,
    maxWidth: '100%',
    align: 'right',
  },
];

function createData(photo, code, name, rest, price, analogs) {
  return { photo, code, rest, name, price, analogs };
}


const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    maxHeight: 440,
  },
});

export const TableList = () => {
  const list = useSelector(selectProductList);
  const rows = list?.map(item => createData(item.photo || null, item.Number, item.DescriptionUkr, item.Rests[0].Value || 0, item.CustomerPrice, item.Analogs || []))

  const classes = useStyles();
  // const [page, setPage] = React.useState(0);
  // const [rowsPerPage, setRowsPerPage] = React.useState(10);

  // const handleChangePage = (event, newPage) => {
  //   setPage(newPage);
  // };

  // const handleChangeRowsPerPage = (event) => {
  //   setRowsPerPage(+event.target.value);
  //   setPage(0);
  // };

  return (
    <Paper className={classes.root}>
      <TableContainer className={classes.container}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              {columns.map((column) => (
                <TableCell
                  key={column.id}
                  align={column.align}
                  style={{ minWidth: column.minWidth, maxWidth: column.maxWidth}}
                >
                  {column.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows?.map((row) => {
              return (
                <Row row={row}/>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <PaginationLink />
    </Paper>
  );
}
