const url = "https://public.omega.page/public/api/v1.0/product/search";
const key = "E65UWV8MoqwJKKrpjf8RtL2KYrin68ia";


export const loadSearchData = (searchQuery, count) => {
  const bodyData = {
    "SearchPhrase": `${searchQuery}`,
    "Rest": 1,
    "From": 0,
    "Count": `${count}`,
    "Key": key
  };

  const options = {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(bodyData),
  }

  return (
    fetch(url, options)
     .then(responce => responce.json()
    )
  ) 
} 

